:root {
  --color-accent-primary: #ff3e79;
  --color-white: #fff;
  --color-text-primary: #1b1c21;
  --color-text-secondary: #7e7e82;
  --color-card-border: #eaeaea;
  --color-accent-secondary: #41f6d7;
  --color-accent-secondary-thin: #d9fff5;



  --color-section-background: #f8f8f8;
  --color-section-header-background: #fdfdfd;
  --font-family: "TTLakes", sans-serif;
}

@mixin visible-bound ($th: 1, $color: dodgerblue) {
  outline: #{$th}px solid #{$color};
  outline-offset: -#{$th}px;
}

// // Extra small  none  <576px
// @mixin media-max-width-XS() {
//   @media (max-width: 576px) {
//     @content;
//   }
// }

// Small  sm  ≥576px
@mixin media-min-width-SM() {
  @media (min-width: 576px) {
    @content;
  }
}

// Medium  md  ≥768px
@mixin media-min-width-MD() {
  @media (min-width: 768px) {
    @content;
  }
}

// Large  lg  ≥992px
@mixin media-min-width-LG() {
  @media (min-width: 1120px) {
    @content;
  }
}

// Extra large  xl  ≥1200px
@mixin media-min-width-XL() {
  @media (min-width: 1366px) {
    @content;
  }
}

// // Extra extra large  xxl  ≥1400px
// @mixin media-min-width-XXL() {
//   @media (min-width: 1440px) {
//     @content;
//   }
// }

@mixin media-hover() {
  @media(hover: hover) {
    &:hover {
      @content;
    }
  }

  @media(hover: none) {
    &:active {
      @content;
    }
  }
}

.header {
  border-bottom: 1px solid var(--color-accent-secondary-thin);
  // background-color: var(--color-white);
  background-color: rgb(255, 217, 0);

  @include media-min-width-LG {
    position: absolute;
    right: 0px;
    left: 0px;
    background-color: transparent;
    border: none;
    background-color: rgb(255, 217, 0);
  }

  &__inner {
    min-height: 88px;
    display: flex;
    justify-content: space-between;
  }

  &__section {
    display: flex;
    align-items: center;
  }

  &__section--left {
    padding: 0 16px 0 0;

    @include media-min-width-SM {
      padding: 0 20px 0 0;
    }

    @include media-min-width-LG {
      display: none;
    }
  }

  &__section--center {
    padding: 0 16px 0 0;
    justify-content: space-between;
    flex-grow: 1;
    position: relative;

    @include media-min-width-SM {
      padding: 0 20px;
    }

    @include media-min-width-MD {
      padding: 0 22px;
    }

    @include media-min-width-LG {
      display: none;
    }

    &::before,
    &::after {
      content: "";
      width: 2px;
      height: 32px;
      position: absolute;
      border-radius: 2px;
      background-color: var(--color-card-border);
    }

    &::before {
      left: -1px;
      display: none;

      @include media-min-width-SM {
        display: block;
      }
    }

    &::after {
      right: -1px;
    }
  }

  &__section--right {
    padding: 0 0 0 20px;
    column-gap: 16px;
    font-size: 0;

    @include media-min-width-LG {
      margin-left: auto;
      column-gap: 31px;
      align-items: end;
      margin-bottom: 6px;
    }
  }

  &__widget-contact {
    display: none;

    @include media-min-width-SM {
      display: flex;
    }

    @include media-min-width-XL {
      // display: none;
    }
  }
}
