*,
::after,
::before {
  box-sizing: border-box;
  // @include visible-bound()
}

html {
  height: 100%;
  background-color: var(--color-section-background);
  // background-image: url("./assets/svg/cell-96-160.svg");
}

body {
  height: 100%;
  min-width: 320px;
  margin: 0;
  font-family: var(--font-family);
  font-weight: 500;
  color: var(--color-text-secondary);
  background-color: var(--color-section-background);
  // background-image: url("./assets/svg/cell-body.svg");
}

a {
  color: inherit;
  text-decoration: none;
  outline-offset: -1px;
}

img {
  max-width: 100%;
  object-fit: cover;
}

ul,
ol {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

button {
  padding: 0;
  border: none;
  background-color: transparent;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  max-width: 1120px;
  margin: 0 auto;
  padding: 0 16px;

  @include media-min-width-MD {
    padding: 0 24px;
  }

  @include media-min-width-XL {
    padding: 0 32px;
  }
}

.page {
  max-width: 1426px;
  margin: 0 auto;
  display: flex;
  position: relative;

  &__overlay {
    display: none;
    position: fixed;
    inset: 0;
    z-index: 10;
    backdrop-filter: grayscale(100%);

    &::before {
      content: "";
      position: absolute;
      inset: 0;
      opacity: 0.9;
      background-color: var(--color-white);
    }

    &--visible {
      display: block;
    }
  }
}

.main {
  position: relative;
  flex-grow: 1;
}
